import Vue from 'vue'
import Router from 'vue-router'
import Home from '../views/Home.vue'
import store from '../store/store'

Vue.use(Router)

const router = new Router({

  routes: [
    {
      path: '/',
      name: 'Home',
      components: {
        default: () => import(/* webpackChunkName: "login" */ '../views/Home.vue'),
        footer: () => import(/* webpackChunkName: "footer" */ '../components/Footer.vue'),
      },
    },
    {
      path: '/login',
      name: 'login',
      components: {
        default: () => import(/* webpackChunkName: "login" */ '../components/Login.vue'),
      },
      meta: {
        title: 'Авторизация'
      }
    },
    {
      path: '/bonus',
      name: 'bonus',
      components: {
        default: () => import(/* webpackChunkName: "schedule" */ '../components/Login.vue'),
        footer: () => import(/* webpackChunkName: "footer" */ '../components/Footer.vue'),
      },
      meta: {
        title: 'Календарь'
      }
    },
  ]
})

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
  // console.log(from, to, to.params.section)


  if (to.name !== 'login' && to.name !== 'registration' && to.name !== 'recovery' && to.name !== 'reset' && !store.state.phone) {
    next()
    setTimeout(() => router.push({ path: '/login' }), 100)
  } else if (to.path === '/') {
    next()
  } else {
    next()
  }
})
export default router
