import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersistence from 'vuex-persist'
import socket from './socket'
// import axios from 'axios'

Vue.use(Vuex)

const vuexLocal = new VuexPersistence({
  key: 'coffee-trend-client',
  namespaced: true,
  storage: window.localStorage,
  reducer: (state) => ({
    phone: state.phone,
    profile: state.profile,
    menu: state.menu,
  }),
})

export default new Vuex.Store({
  state: {
    phone: '+79139466233',
    phoneVerification: '',
    profile: {
      nickname: '',
      avatar: '',
      favorites: '',
    },
    menu: [
      {title: 'Напитки', list: []},
      {title: 'Завтрак', list: []},
      {title: 'Выпечка', list: []},
    ],
    menuUpdated: 0,
  },
  mutations: {
    SOCKET_PROFILE (state, profile) {
      console.log('SOCKET_PROFILE', profile)
      state.profile = profile
    },
    setPhone(state, {phone, phoneVerification}) {
      state.phone = phone
      state.phoneVerification = phoneVerification
    },
  },
  actions: {
  },
  modules: {
    socket,
  },
  plugins: [
    vuexLocal.plugin
  ]
})
